import React from 'react';
import { Button, Snackbar, Alert } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const InstallPrompt: React.FC = () => {
  const [showPrompt, setShowPrompt] = React.useState(false);
  const [deferredPrompt, setDeferredPrompt] = React.useState<any>(null);
  const theme = useTheme();

  React.useEffect(() => {
    const handler = (e: Event) => {
      e.preventDefault();
      setDeferredPrompt(e);
      setShowPrompt(true);
    };

    window.addEventListener('beforeinstallprompt', handler);

    return () => {
      window.removeEventListener('beforeinstallprompt', handler);
    };
  }, []);

  const handleInstall = async () => {
    if (!deferredPrompt) return;

    deferredPrompt.prompt();
    const { outcome } = await deferredPrompt.userChoice;
    
    if (outcome === 'accepted') {
      console.log('User accepted the install prompt');
    } else {
      console.log('User dismissed the install prompt');
    }
    
    setDeferredPrompt(null);
    setShowPrompt(false);
  };

  const handleClose = () => {
    setShowPrompt(false);
  };

  // Check if the app is already installed
  const isStandalone = window.matchMedia('(display-mode: standalone)').matches;
  const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent);

  if (isStandalone || isIOS) {
    return null;
  }

  return (
    <Snackbar
      open={showPrompt}
      anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      onClose={handleClose}
    >
      <Alert
        severity="info"
        onClose={handleClose}
        action={
          <Button color="primary" size="small" onClick={handleInstall}>
            Install
          </Button>
        }
        sx={{
          width: '100%',
          bgcolor: theme.palette.mode === 'dark' ? 'background.paper' : 'info.light',
          color: theme.palette.mode === 'dark' ? 'text.primary' : 'info.contrastText',
        }}
      >
        Install this app on your device for quick and easy access!
      </Alert>
    </Snackbar>
  );
};

export default InstallPrompt; 