import { BrowserRouter as Router } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import { Brightness4, Brightness7 } from '@mui/icons-material';
import { useState, useMemo } from 'react';
import Navigation from './components/Navigation';
import FeedingScreen from './components/FeedingScreen';
import HistoryScreen from './components/HistoryScreen';
import WeightScreen from './components/WeightScreen';
import InstallPrompt from './components/InstallPrompt';

function App() {
  const [mode, setMode] = useState<'light' | 'dark'>('dark');
  const [activeTab, setActiveTab] = useState(0);

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode,
          primary: {
            main: '#3f51b5',
          },
          secondary: {
            main: '#f50057',
          },
        },
      }),
    [mode],
  );

  const toggleColorMode = () => {
    setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router basename="/BabyFeedingDiary">
        <Box sx={{ 
          bgcolor: 'background.default',
          minHeight: '100vh',
          pb: 7
        }}>
          <Box sx={{ position: 'fixed', right: 16, top: 16, zIndex: 1000 }}>
            <IconButton onClick={toggleColorMode} color="inherit">
              {mode === 'dark' ? <Brightness7 /> : <Brightness4 />}
            </IconButton>
          </Box>

          {activeTab === 0 && <FeedingScreen />}
          {activeTab === 1 && <HistoryScreen />}
          {activeTab === 2 && <WeightScreen />}

          <Navigation activeTab={activeTab} onTabChange={setActiveTab} />
          <InstallPrompt />
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App; 